const FORM_MODES = Object.freeze({
    DISP_NONE: 1,
    DISP_DIST_PROMPTS: 2,
    DISP_PROMPTS: 3
})

const INIT_RADIO_STATE = ""
const INIT_CHECKBOX_STATE = []
// const INIT_PROMPT_STATES = [INIT_RADIO_STATE, INIT_CHECKBOX_STATE]

